<template>
  <div class="index">
    <div class="header-top flex-between">
      <div class="logo-box flex-item flex-middle">
        <img class="logo" src="@/assets/images/logo.png" alt="">
         <div class="name">TIME DEFI</div> 
      </div>
      <div class="wallet-btn" v-if="address">
        {{ address.substring(0, 4) + '....' + address.substring(address.length - 4, address.length) }}
      </div>
       <div class="wallet-btn" v-else @click="initWallet">
        链接钱包
      </div>
    </div>
    <div class="detail-box">
        <div class="time-info-box">
      <div class="title">TIME DEFI</div>
      <div class="time-info">
        <div class="item flex-middle">
          <img src="@/assets/images/time.png" alt="">
          <div class="text">300天挖矿周期</div>
        </div>
      </div>
      <div class="info-text">TIME DEFI生态治理通证,质押LP挖矿产出</div>
    </div>
    </div>
   
    <div class="floor-top">
      <!-- <div class="address-box" v-if="address">
        地址：{{ address.substring(0, 14) + '......' + address.substring(address.length - 14, address.length) }}
      </div> -->
      <div class="data-box">
        <div class="data-top">
          <div class="num">{{total}}</div>
          <div class="desc">总供应量</div>
        </div>
        <div class="data-bottom flex-middle">
          <div class="item flex-item">
            <div class="desc">矿池总量</div>
            <div class="amount">{{lpAmount}}</div>
          </div>
          <div class="line"></div>
          <div class="item flex-item">
            <div class="desc">已产出量</div>
            <!-- <div class="amount">{{rewardAmount}}</div> -->
            <div class="amount">100.000</div>
          </div>
        </div>
      </div>
      <div class="index-card-wrap">
        <div class="index-card" v-for="(item, index) in typeInfo">
          <div class="title flex-middle flex-between">
            <div class="left">可領取</div>
            <div class="right">{{item.symbol}} 挖矿({{item.rewardsTokenSymbol}})</div>
          </div>
          <div class="num">{{Number(item.rewards / 10**item.rewardsTokenDecimals).toFixed(4)}}</div>
          <!-- <div class="info">{{$t('index.key_a總質押量key_b',{key_a: 'LP', key_b: lpStakeInfo.totalSupply})}}</div> -->
          <div class="stake-info flex-between">
            <div class="item">
              <div class="name">总质押量</div>
              <div class="amount">{{
                Number(item.totalSupply / 10**item.stakingTokenDecimals) > 0.0001 || Number(item.totalSupply)==0 ?
                Number(item.totalSupply / 10**item.stakingTokenDecimals).toFixed(4)
                : toScientificNum(Number(item.totalSupply / 10**item.stakingTokenDecimals))
                }}</div>
            </div>
             <div class="item">
              <div class="name">我的质押量</div>
              <div class="amount">{{
                Number(userInfo[index].balances / 10**item.stakingTokenDecimals) > 0.0001 || Number(userInfo[index].balances)==0?
                Number(userInfo[index].balances / 10**item.stakingTokenDecimals).toFixed(4)
                : toScientificNum(Number(userInfo[index].balances / 10**item.stakingTokenDecimals))
                }}</div>
            </div>
            <div class="item">
              <div class="name">推荐收益</div>
              <div class="amount">{{Number(userInfo[index].introReward / 10**item.rewardsTokenDecimals).toFixed(4)}}</div>
            </div>
            <div class="item">
              <div class="name">APY</div>
              <div class="amount">{{item.apy}}%</div>
            </div>
          </div>
          <div class="btn-wrap">
            <div class="btn" @click="getReward(item.stakingToken)">领取</div>
            <div class="btn" @click="stakeClick(index)">质押</div>
            <div class="btn" @click="withdrawClick(index)">解押</div>
          </div>
        </div>
        <!-- <div class="nums flex">
          <div class="item border-1px-r" @click="$router.push({name:'RecordLpPower'})">
            <div class="key">{{$t('index.已質押',{key:'LP'})}}</div>
            <div class="value">{{Number(lpStakeInfo.stakeLpAmount).toFixed(4)}}</div>
          </div>
          <div class="item" @click="$router.push({name:'RecordLpMiningr'})">
            <div class="key">{{$t('index.累計挖礦')}}</div>
            <div class="value">{{lpStakeInfo.haveTake}}</div>
          </div>
        </div> -->
      </div>
       <div class="share-box" v-if="typeInfo.length">
           <div class="title">分享推广</div>
           <div class="link">{{link}}</div>
           <div class="copy-btn" :data-clipboard-text="link">复制分享链接</div>
       </div>
    </div>
  </div>
  <van-action-sheet v-model:show="isPledgeB">
    <div class="sheet-wrap-index">
      <div class="title">
        <div class="text">质押</div>
        <img class="close" @click="isPledgeB = false" src="@/assets/images/close@2x.png" alt="">
      </div>
      <div class="content">
        <div class="input-wrap flex-middle flex-between">
          <van-field :clearable="true" placeholder="请输入质押数量" v-model="lpStakeInfo.amount">
          </van-field>
          <div class="label" @click="lpStakeInfo.amount = lpStakeInfo.lpBalance">最大</div>
        </div>
        <div class="des">可用{{lpStakeInfo.stakeLpSympol}}:{{Number(lpStakeInfo.lpBalance).toFixed(18)}}</div>
      </div>
      <div class="btn-wrap" style="margin-top:15px;">
        <van-button style="width:100%" v-if="Number(lpStakeInfo.allowance) > 0" type="primary" :loading="btnLoading" size="large" loading-text="确定中..." @click="stake">确定</van-button>
        <van-button style="width:100%" v-else :loading="btnLoading" type="primary" size="large" loading-text="授权中..." @click="approveLp(lpStakeInfo.stakeToken)">授权{{lpStakeInfo.stakeLpSympol}}</van-button>
      </div>
    </div>
  </van-action-sheet>
  <!-- 弹窗解押 -->
  <van-action-sheet v-model:show="isRelease">
    <div class="sheet-wrap-index">
      <div class="title">
        <div class="text">解押:{{Number(lpStakeInfo.stakeLpAmount).toFixed(18)}}</div>
        <img class="close" @click="isRelease = false" src="@/assets/images/close@2x.png" alt="">
      </div>
      <div class="content">
        <div class="input-wrap flex-middle flex-between">
          <van-field :clearable="true" v-model="amountRelease" placeholder="请输入解押数量">
          </van-field>
          <div class="label" @click="amountRelease = lpStakeInfo.stakeLpAmount">最大</div>
        </div>
      </div>
      <div class="btn-wrap" style="margin-top:20px;">
        <van-button @click="withdraw" type="primary" style="width:100%" :loading="btnLoading" size="large" loading-text="确定中...">确定</van-button>
      </div>
    </div>
  </van-action-sheet>
</template>
<script>

import { reactive, toRefs, computed, watch, onMounted } from "vue";
import { useI18n } from "vue-i18n";
import { useStore } from "vuex";
import config from "@/utils/config";
import LpStakeAbi from "@/utils/LpStakeAbi";
import routerPathAbi from "@/utils/routerPath";
import tokenAbi from "@/utils/abi";
import { Toast } from 'vant';
import Clipboard from 'clipboard'
import { MaxUint256 } from "@ethersproject/constants";
import BigNumber from 'bignumber.js';
import { intro } from "@/storage"
import web3Data from "@/utils/initWeb3";
import {
  Multicall
} from 'ethereum-multicall';
export default {
  setup () {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      balance: 0,
      LpStakeContract: null,
      routerPathContract: null,
      intro: intro.get() || config.rootAddress,
      link: '',
      typeInfo: [],
      userInfo: [],
      lpStakeInfo: {
        stakeToken: '',
        allowance: '0',
        lpBalance: 0,
        stakeLpAmount: 0,
        rewardAmount: 0,
        stakeLpSympol: '',
        deciamls: '',
        amount: ''
      },
      isPledge: false, //质押弹窗
      isPledgeB: false,
      isRelease: false,
      amountRelease: '',
      btnLoading: false,
      total: 0.00,
      lpAmount: 0.000,
      rewardAmount: 0.00,
      initContract: () => {
        data.LpStakeContract = new data.web3.eth.Contract(LpStakeAbi, config.lpStakeAddress)
        data.routerPathContract = new data.web3.eth.Contract(routerPathAbi, config.routerPath)
        data.initData()
      },
      symbolList: [
        {
          isLp: true,
          symbol: 'HT-TIME',
          amount: 80,
          decimals: 14
        },
        {
          isLp: false,
          symbol: 'TIME',
          amount: 20,
          decimals: 9
        }
      ],
      initWallet: () => {
        web3Data.initWeb3()
      },
      initData: () => {
        data.link = `${window.location.origin}${window.location.pathname}#/?intro=${data.address}`
        data.getAllStakeType()
        data.getAllStakeTypeUserInfo()
        data.getIntro()
        data.getDataInfo()
        setInterval(() => {
          data.getAllStakeType()
        data.getAllStakeTypeUserInfo()
          data.getDataInfo()
        }, 3000)
      },
      getDataInfo: async () => {
        let rewardsTokenContract = new data.web3.eth.Contract(tokenAbi, config.spaceTimeAddress)
        let decimals = await rewardsTokenContract.methods.decimals().call()
        let totalSupply = await rewardsTokenContract.methods.totalSupply().call()
        let startTime = await data.LpStakeContract.methods.startTime().call()
        let time = parseInt(new Date().getTime() / 1000) - startTime;
        let lpAmount = await rewardsTokenContract.methods.balanceOf(config.lpAddress).call()
        data.total = new BigNumber(totalSupply).dividedBy(10 ** decimals).toFixed(3)
        data.lpAmount =  new BigNumber(lpAmount).dividedBy(10 ** decimals).toFixed(3)
        data.rewardAmount = new BigNumber(0.0015).multipliedBy(time).toFixed(3)
      },
      toScientificNum:(num) => {
        var eformat = num.toExponential();
        var tmpArray = eformat.match(/(.?\d\.?\d*)e(.?\d*)/);
        // return [Number(tmpArray[1]), Number(tmpArray[2])];
        return `${Number(tmpArray[1]).toFixed(2)}x10^${tmpArray[2]}`
    },
      getIntro: async () => {
        let intros = await data.LpStakeContract.methods.intros(data.address).call()
        console.log(intros, 'intros')
      },
      getAllStakeType: async () => {
        let types = await data.LpStakeContract.methods.getAllStakeType().call()
        
       
        let arr = []
        for(let i = 0; i < types.length; i++) {
          // let rewardsTokenContract = new data.web3.eth.Contract(tokenAbi, types[i].rewardsToken)
          // let stakingTokenContract = new data.web3.eth.Contract(tokenAbi, types[i].stakingToken)
          // let rewardsTokenSymbol = await rewardsTokenContract.methods.symbol().call()
          // let rewardsTokenDecimals = await rewardsTokenContract.methods.decimals().call()
          // let stakingTokenSymbol = await stakingTokenContract.methods.symbol().call()
          // let stakingTokenDecimals = await stakingTokenContract.methods.decimals().call()
          // let rewards = await data.LpStakeContract.methods.earned(types[i].stakingToken, data.address).call()
          // let usdtAmount = await data.routerPathContract.methods.getLpSwapToken(types[i].stakingToken, types[i].totalSupply, config.usdtAddress).call()
          // let income = new BigNumber(types[i].rewardPerTime).multipliedBy(86400).multipliedBy(360).toFixed()
          // let rewardAmount = await data.routerPathContract.methods.getBestPathAmount(types[i].rewardsToken, config.usdtAddress, income).call()
            let income = new BigNumber(types[i].rewardPerTime).multipliedBy(86400).multipliedBy(360).toFixed()
          const contractCallContext = [
            {
              reference: 'rewardsTokenContract',
              contractAddress: types[i].rewardsToken,
              abi: tokenAbi,
              calls: [{ reference: 'rewardsTokenSymbol', methodName: 'symbol', methodParameters: []}, { reference: 'rewardsTokenDecimals', methodName: 'decimals', methodParameters: []}]
            },
            {
              reference: 'stakingTokenContract',
              contractAddress: types[i].stakingToken,
              abi: tokenAbi,
              calls: [{ reference: 'stakingTokenSymbol', methodName: 'symbol', methodParameters: []}, { reference: 'stakingTokenDecimals', methodName: 'decimals', methodParameters: []}]
            },
            {
              reference: 'routerPathContract',
              contractAddress: config.routerPath,
              abi: routerPathAbi,
              calls: [{ reference: 'usdtAmount', methodName: data.symbolList[i].isLp ?'getLpSwapToken': 'getBestPathAmount', methodParameters: data.symbolList[i].isLp ? [types[i].stakingToken, Number(types[i].totalSupply) <= 0 ? 1 + '0'.repeat(data.symbolList[i].decimals) : types[i].totalSupply, config.usdtAddress]: [types[i].stakingToken, config.usdtAddress, Number(types[i].totalSupply) <= 0 ? 1 + '0'.repeat(data.symbolList[i].decimals) : types[i].totalSupply]}, 
              { reference: 'rewardAmount', methodName: 'getBestPathAmount', methodParameters: [types[i].rewardsToken, config.usdtAddress, income]}]
            }
             ,
             {
              reference: 'lpStakeContract',
              contractAddress: config.lpStakeAddress,
              abi: LpStakeAbi,
              calls: [{ reference: 'rewards', methodName: 'earned', methodParameters: [types[i].stakingToken, data.address]}]
            },
          ]
          
          const multicall = new Multicall({ multicallCustomContractAddress: config.multicallAddress,web3Instance: data.web3, tryAggregate: false });
           const list = await multicall.call(contractCallContext);
           console.log(list)
           let rewardsTokenSymbol = list.results.rewardsTokenContract.callsReturnContext[0].returnValues[0]
          let rewardsTokenDecimals = list.results.rewardsTokenContract.callsReturnContext[1].returnValues[0]
          let stakingTokenSymbol = list.results.stakingTokenContract.callsReturnContext[0].returnValues[0]
          let stakingTokenDecimals = list.results.stakingTokenContract.callsReturnContext[1].returnValues[0]
          let rewards = data.web3.utils.hexToNumberString(list.results.lpStakeContract.callsReturnContext[0].returnValues[0].hex)
          let usdtAmount = data.web3.utils.hexToNumberString(list.results.routerPathContract.callsReturnContext[0].returnValues[0].hex)
          let rewardAmount = data.web3.utils.hexToNumberString(list.results.routerPathContract.callsReturnContext[1].returnValues[0].hex)
          rewardAmount = Number(rewardAmount) > 0 ? rewardAmount : 20 * 10**18 * data.symbolList[i].amount;
          let params = {
            rewardsTokenSymbol: rewardsTokenSymbol,
            rewardsTokenDecimals: rewardsTokenDecimals,
            stakingTokenSymbol: stakingTokenSymbol,
            stakingTokenDecimals: stakingTokenDecimals,
            rewards: rewards,
            apy: Number(rewardAmount * 100/usdtAmount).toFixed(1),
            symbol: data.symbolList[i].symbol
          }
          arr.push(Object.assign({}, types[i], params))
        }
        data.typeInfo = arr;
        console.log(data.typeInfo)
      },
      getAllStakeTypeUserInfo: async () => {
         let infos = await data.LpStakeContract.methods.getAllStakeTypeUserInfo(data.address).call()
         data.userInfo = infos;
         console.log(data.userInfo)
      },
      getLpAllowance: async (token) => {
        let stakingTokenContract = new data.web3.eth.Contract(tokenAbi, token)
        let result = await stakingTokenContract.methods.allowance(data.address, config.lpStakeAddress).call();
        data.lpStakeInfo.allowance = result;
      },
  
      approveLp: async (token) => {
        data.btnLoading = true
        try {
          let stakingTokenContract = new data.web3.eth.Contract(tokenAbi, token)
          await stakingTokenContract.methods.approve(config.lpStakeAddress, MaxUint256).send({ from: data.address });
          data.getLpAllowance(token)
          data.btnLoading = false
        } catch (error) {
            console.log(error)
          data.btnLoading = false
        }

      },
      approveUsdt: async () => {
        data.btnLoading = true
        try {

          await data.UsdtContract.methods.approve(config.uvStakeAddress, MaxUint256).send({ from: data.address });
          data.getUsdtAllowance()
          data.btnLoading = false
        } catch (error) {
          data.btnLoading = false
        }

      },
      getReward: async (stakingToken) => {
        await data.LpStakeContract.methods.getReward(stakingToken).send({ from: data.address });
        data.initData()
      },
      stakeClick: async (index) => {
        let stakingTokenContract = new data.web3.eth.Contract(tokenAbi, data.typeInfo[index].stakingToken)
        data.lpStakeInfo.allowance = await stakingTokenContract.methods.allowance(data.address, config.lpStakeAddress).call();
        let lpBalance = await stakingTokenContract.methods.balanceOf(data.address).call();
        let deciamls = Number(data.typeInfo[index].stakingTokenDecimals)
        data.lpStakeInfo.deciamls = deciamls
        data.lpStakeInfo.stakeToken = data.typeInfo[index].stakingToken
        data.lpStakeInfo.lpBalance = new BigNumber(lpBalance).dividedBy(10 ** deciamls).toFixed(deciamls)
        data.lpStakeInfo.stakeLpAmount = new BigNumber(data.userInfo[index].balances).dividedBy(10 ** deciamls).toFixed(deciamls)
        data.lpStakeInfo.stakeLpSympol = data.typeInfo[index].symbol
        console.log(data.lpStakeInfo)
        data.isPledgeB = true
      },
      stake: async () => {
        if (Number(data.lpStakeInfo.amount) <= 0) {
          Toast('数量必须大于0')
          return
        }
        data.isPledgeB = false
        let amount = new BigNumber(data.lpStakeInfo.amount).multipliedBy(Math.pow(10, data.lpStakeInfo.deciamls)).toFixed();
        if (Number(data.lpStakeInfo.amount) > Number(data.lpStakeInfo.lpBalance)) {
          Toast('余额不足')
          return
        }
        data.btnLoading = true
        try {

          await data.LpStakeContract.methods.stake(data.lpStakeInfo.stakeToken,amount,data.intro).send({ from: data.address });
          Toast('质押成功')
          data.btnLoading = false
          data.initData()
        } catch (error) {
          data.btnLoading = false
        }

      },
      withdrawClick: async (index) => {
         console.log(index)
          let stakingTokenContract = new data.web3.eth.Contract(tokenAbi, data.typeInfo[index].stakingToken)
        data.lpStakeInfo.allowance = await stakingTokenContract.methods.allowance(data.address, config.lpStakeAddress).call();
        let lpBalance = await stakingTokenContract.methods.balanceOf(data.address).call();
        let deciamls = Number(data.typeInfo[index].stakingTokenDecimals)
        data.lpStakeInfo.deciamls = deciamls
        data.lpStakeInfo.stakeToken = data.typeInfo[index].stakingToken
        data.lpStakeInfo.lpBalance = new BigNumber(lpBalance).dividedBy(10 ** deciamls).toFixed(deciamls)
        data.lpStakeInfo.stakeLpAmount = new BigNumber(data.userInfo[index].balances).dividedBy(10 ** deciamls).toFixed(deciamls)
        data.lpStakeInfo.stakeLpSympol = data.typeInfo[index].symbol
          data.isRelease = true
      },
      withdraw: async () => {
        if (Number(data.amountRelease) > Number(data.lpStakeInfo.stakeLpAmount)) {
          Toast('余额不足')
          return
        }
        let amount = new BigNumber(data.amountRelease).multipliedBy(Math.pow(10, data.lpStakeInfo.deciamls)).toFixed();
        data.btnLoading = true
        try {
           data.isRelease = false
           await data.LpStakeContract.methods.withdraw(data.lpStakeInfo.stakeToken,amount).send({ from: data.address });
           Toast('解押成功')
           data.btnLoading = false
          data.initData()
        } catch (error) {
          console.log(error)
           data.btnLoading = false
        }
      }
    })
    if (data.address) {
      data.initContract()
    }
    onMounted(() => {
      var clipboard = new Clipboard('.copy-btn')
      clipboard.on('success', function (e) {
        Toast('复制成功')
        e.clearSelection()
      })
      clipboard.on('error', function () {
        Toast('复制失败,请长按复制')
      })
    })
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
<style lang="less" scoped>
.header-top{
  padding: 16px;
  .name{
    color: #42BAE4;
    font-size: 16px;
    font-weight: 600;
  }
  .logo{
     width: 40px;
  }
  .wallet-btn{

    height: 28px;
          border-radius: 14px;
          width: 90px;
          background: #000000;
          position: relative;
          font-size: 12px;
          line-height: 28px;
          color: rgba(255, 255, 255, 0.8);
          text-align: center;
          &:after { 
            position: absolute;
            z-index: -1;
            top: -1px; 
            bottom: -1px;
            right: -1px; 
            left: -1px;
            background: linear-gradient(90deg, #A8098E, #2FDCF5, #A8098E);
            border-radius: 14px;
            content: '';
          }
  }
}

.data-box{
  padding: 16px;
  background: #282862;
  border-radius: 6px;
  text-align: center;
  margin-bottom: 15px;
  .data-top{
    padding-bottom: 12px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    .num{
      font-size: 28px;
      font-weight: 600;
      color: #42BAE4;
      margin-bottom: 4px;
    }
    .desc {
      color: rgba(255, 255, 255, 0.4);
      font-size: 13px;
    }
    
  }
  .line{
    height: 30px;
    width: 1px;
    background: rgba(255, 255, 255, 0.4);
  }
  .data-bottom{
    padding-top: 12px;
    .desc{
      color: rgba(255, 255, 255, 0.4);
      font-size: 13px;
    }
    .amount{
      font-size: 18px;
      font-weight: 600;
      color: #42BAE4;
      margin-top: 4px;
    }
  }
}
.detail-box{
  padding: 16px;
  background: url(~@/assets/images/time_bg.png) no-repeat right;
  background-size: 75%;
}
.time-info-box{
   padding: 20px 0;
  .title{
    color: #fff;
    font-size: 20px;
    font-weight: 600;
  }
  .info-text{
    font-size: 13px;
    color: rgba(255, 255, 255, 0.8);
    line-height: 22px;
  }
  .time-info{
    margin: 8px;
    img{
      width: 14px;
      margin-right: 5px;
    }
    .text{
      font-size: 13px;
      color: #42BAE4;
    }
  }
}
.index {
  min-height: 100vh;
  overflow: auto;
  // background: #fff;
  display: flex;
  flex-direction: column;
}
.floor-top {
  flex-shrink: 0;
  padding: 20px 16px 20px;
  word-break: break-all;
  // background: #fff;
  .index-card-wrap {
    padding-bottom: 16px;
    position: relative;
    .index-card {
      position: relative;
      z-index: 1;
      overflow: hidden;
      margin-bottom: 15px;
      border-radius: 6px;
      min-height: 140px;
      color: #42BAE4;
      line-height: 1;
      padding: 15px 20px;
      background: url(~@/assets/images/stake_box.png);
      background-size: 100% 100%;
      .info {
        padding-bottom: 7px;
        font-size: 13px;
      }
      .num {
        font-size: 28px;
        font-weight: 700;
        padding: 12px 0 9px;
